@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300;400&display=swap);

nav {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    width: 100vw;
    height: 12vh;
    padding-left: 8rem;
}

.nav-items {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-content: center;
            align-content: center;
    width: 85%;
    height: 100%;
    padding-left: 1rem;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}

.nav-item {
    font-size: 1.4rem;
    list-style: none;
    font-weight: 300;
}

.logo {
    height: 72px;
}

.ebay-button {
    height: 48px;
    width: 200px;
    padding: 8px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    background-color: #F17D21;
    color: white;
    text-decoration: none;
    font-size: 1.3rem;
    border-radius: 6px;
    margin-left: 7rem;
    box-shadow: 0 15px 30px 5px rgba(0, 0, 0, 0.3);
  }
  
  .ebay-button:hover {
    background-color: #e4771d;
    color: white;
    transition: 0.3s;
  }

.nav-button { 
    right: 0;
    margin-left: 55%; 
} 

.show-on-mobile a {
    display: none;
}

@media (max-width: 1344px) {
    .nav-button {
        margin-left: 10%;
    }
} 

@media (max-width: 700px) {
    nav {
        padding: 1rem;
        height: 15vh;
    }

    .nav-items {
        display: block;
        text-align : right;
    }

    .nav-item {
        padding-bottom: 0.4rem;
    }

    .nav-button {
        display: none;
    }
    .show-on-mobile a {
        display: block;
    }
}
.header-container {
    display: grid;
    width: 100vw;
    overflow-x: hidden;
    height: 600px;
    padding: 8rem;
    background-image: url(/static/media/parts-filter.741c86bf.png);
    background-repeat: no-repeat;
    background-size: cover;
    grid-template-areas:
    "heading"
    "description"
    "button";
    grid-template-columns: 100%;
    color: whitesmoke;
    font-family: 'Roboto Condensed', sans-serif;
}

.header-title {
    grid-area: heading;
    margin-top: 2rem;
    font-size: 4rem;
    width: 100%;
}

.header-description {
    width: 79%;
    grid-area: description;
    font-weight: 300;
    font-size: 2.4rem;
    padding-left: 6.8rem;
    padding-top: 1.5rem;
}

.long-arrow {
    grid-area: description;
    margin-left: 79%;
    margin-top: 3rem;
    width: 30%; 
}

@media (max-width: 975px) {
    .header-description {
    padding-left: 2.8rem;
    font-size: 2rem;
    }
}

@media (max-width: 800px) {
    .header-container {
       display: block;
       height: 556px;
       padding: 4rem 0;
       text-align: center;
    }
    .header-title {
        margin-bottom: 2rem;
        font-size: 3.5rem;
    }

    .header-description {
        width: 100%;
        padding: 0;
    }

    .header-button {
        margin: 2rem auto 1rem;
    }

    .long-arrow {
        position: absolute;
        width: 70%;
        min-width: 400px;
        z-index: 1;
        bottom: 160px;
        right: 240px;
        top: 480px;
    }

}
.product {
    width: 40%;
    margin: 0 auto;
    text-align: center;
}

.product-title {
    font-size: 2.4rem;
    margin: 1.6rem auto;
}
.product-description {
    font-size: 1.6rem;
    font-weight: 300;
    margin: 1rem auto;

}
.auction-nudge {
    font-family: 'Roboto Condensed', sans-serif;
    width: 90%;
    margin: 0 auto;
}
#an-item-wrap {
    padding-left: 5%;
}


#an-cats-nav,
#an-search-box {
    height: 40px;
    width: 260px;
    font-size: 1.2rem;
    padding: 2px;
    font-family: 'Roboto Condensed', sans-serif;
    border-color: 1px solid black;
}

#auction-nudge-items a {
    color: #434141;
    font-size: 1.2rem;
    margin: 0 auto;  
}

#an-search-submit {
    height: 38px;
}

div#auction-nudge-items div#an-search-submit {
	background-color: #F17D21;
	color: white;
    border-radius: 4px;
}	

div#auction-nudge-items ul.an-page-wrap {
    background-color: #537188;
    color: white;
    border-radius: 4px;
  }
.an-amount {
    font-size: 1.4rem;
}

div#auction-nudge-items .an-view a {
    color: orange;
}
.reviews {
    width: 40%;
    margin: 0 auto;
    text-align: center;
}

.review-title {
    font-size: 2.4rem;
    margin: 1.6rem auto;
}
.review-description {
    font-size: 1.6rem;
    font-weight: 300;
    margin: 1rem auto 3rem;
}

.feedback {
    width: 60%;
    margin: 0 auto;
    padding-left: 6rem;
}

.feedback td {
    background: white;
}

.feedback td.second {
    font-size: 1.2rem;
    font-weight: 300;
}

.auction-nudge {
    font-family: 'Roboto Condensed', sans-serif;
    width: 90%;
    margin: 10px auto;
}
#auction-nudge-items {
    margin: 0 auto;
    padding: 0;
}

@media (max-width: 900px) {
    .reviews {
        width: 80%;
    }
    .feedback {
        width: 100%;
        padding: 0;
    }
}
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  width: 100vw;
  font-family: 'Roboto Condensed', sans-serif;
  overflow-x: hidden;
}

a {
  color: black;
  text-decoration: none;
  list-style: none;
}

a:hover {
  color: #F17D21;
}


.contact-title {
    font-size: 2.4rem;
    text-align: center;
    margin-top: 8rem;
   
}
.contact-container{
    display: grid;
    height: 52vh;
    grid-template-columns: 1fr 50%;
    width: 100%;
    text-align: center;
    margin: 4rem auto;
}

.question {
    font-size: 1.2rem;
    font-weight: 300;
    margin: 0.5rem auto;
    padding-right: 20%;
}


.contact-info-container {
    border-left: 4px solid gray;
    height: 30%;
    text-align: center; 
}

.contact {
    font-weight: 300;
    font-size: 1.2rem;
    padding-left: 0.5rem;

}

.contact-info {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    padding-left: 30%;
}

.icon {
   height: 40px;
}

@media(max-width: 700px) {
    .contact-container {
        padding-top: 2rem;
        display: block;
        text-align: center;
    }
    .question {
        padding-right: 0;
        text-align: center;
        margin-top: 0.8rem;
    }
    .contact-info-container {
        padding-top: 2rem;
        border-top: 4px solid gray;
        border-left: 0;
        width: 70%;
        margin: 2rem auto 0;
    }

    .contact-info {
        padding-left: 0;
        -webkit-justify-content: center;
                justify-content: center;
    }
}

.footer-container {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    padding-left: 3rem;
    height: 20vh;
    background-color: #537188;
}

.footer {
    font-weight: 300;
    list-style: none;
}

.footer-link {
    color: white;
    font-size: 1.2rem;
    padding: 0.2rem;  
}

/* .footer-link:hover {
   color: #F17D21;
} */

.footer a {
    text-decoration: none;
    color: white;
    cursor: pointer;
}

.footer a:hover {
    color: #F17D21;
}
